  width: 40px;
  height: 40px;
  border-color: #d63638;
  color: #d63638;
  margin: 0;
  padding: 0;
}
#root .remove-detail-btn:after{
  content: '+';
  font-size: 40px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  line-height: 0;
