}
#root .detail-plan svg text{
  /* font-size: 26px; */
}
#root .detail-settings{
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  border: 1px solid #ddd;
  -webkit-align-self: flex-start;
          align-self: flex-start;
}
#root .detail-settings label span{
  font-size: 12px;
}
#root .detail-settings label span b{
  color: #3582c4;
  font-size: 12px;
}
#root .detail-ext-options{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
