}
#root .angle-options-wrapper{
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  position: relative;
  padding: 5px;
  border: 1px solid #ddd;
  margin: 5px;
  min-height: 36px;
  width: calc(50% - 22px);
  
}
#root .angle-options-wrapper.active{
  background-color: #f7f7f7;
  width: calc(100% - 22px);
}
#root .angle-options-wrapper label{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
#root .angle-options__send{
  position: absolute;
  right: 0;
  top: 50px;
}
#root .angle-options-wrapper label:not(.switch){
  -webkit-align-items: center;
          align-items: center;
}
#root .angle-options-wrapper input[type=number]{
  margin: 0 10px;
}
#root .angle-options-type{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 10px;
}
#root .angle-options-type button{
  background-color: #fff;
  border: 1px solid #3582c4;
  color: #3582c4;
  cursor: pointer;
  padding: 5px;
  font-size: 12px;
}
#root .angle-options-type button[data-active=active]{
  background-color: #3582c4;
  border: 1px solid #3582c4;
  color: #fff;
}
#root .angle-type__linear{
  margin: 0 5px;
}

#root .angle-options__tabs-header{
  margin-top: 20px;
}
#root .angle-options__tabs-header button.active{
  background-color: #3582c4;
  border: 1px solid #3582c4;
  color: #fff;
}
#root .angle-options__tabs-header button[disabled]{
  opacity: .5;
  cursor: default;
}
#root .angle-title-wrapper{
  margin: 0;
}
#root .detail-settings .angle-title{
  width: 150px;
  display: inline-block;
  font-size: 14px;
  padding-left: 50px;
  line-height: 1;
  box-sizing: border-box;
}
#root .angle-title::before{
  content: '';
  background-color: #eee;
  height: 35px;
  width: 35px;
  position: absolute;
  top: 0;
  left: 0;
}
#root .angle-title::after{
  content: '';
  border: 10px solid transparent;
  border-left: 10px solid #d63638;
  border-top: 10px solid #d63638;
  max-height: 0;
  max-width: 0;
  position: absolute;
  display: inline-block;
  top: 1px;
  left: 1px;
}
#root .angle-options-wrapper.active .angle-title::before{
  background-color: transparent;
}
#root .angle-title.rt::after{
  border-left: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 10px solid #d63638;
  border-top: 10px solid #d63638;
  top: 1px;
  left: 13px;
}
#root .angle-title.rb::after{
  border-left: 10px solid transparent;
  border-bottom: 10px solid #d63638;
  border-right: 10px solid #d63638;
  border-top: 10px solid transparent;
  top: 13px;
  left: 13px;
}
#root .angle-title.lb::after{
  border-left: 10px solid #d63638;
  border-bottom: 10px solid #d63638;
  border-right: 10px solid transparent;
  border-top: 10px solid transparent;
  top: 13px;
  left: 1px;
}

#root label input:not([type=checkbox]){
  width: calc(100% - 12px);
}
#root label>input[type=number]{
  min-width: 60px;
  font-size: 13px;
}
#root label>select{
  min-width: 100px;
  font-size: 13px;
}
#root .switch{
  position: absolute;
  right: 5px;
  top: 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

#root button{
  margin: 0 5px;
  padding: 7px 15px;
  font-size: 16px;
  background-color: #fff;
  border: 1px solid #3582c4;
  color: #3582c4;
  cursor: pointer;
  transition: all 0.3s;
}
#root button:disabled{
  opacity: 0.5;
  cursor: default;
}
#root button:not([disabled]):hover{
  box-shadow: 1px 3px 3px #999;
  background-color: #3582c4;
  color: #fff;
}
#root button.remove-detail-btn:hover{
  background-color: #d63638;
}
#root button.remove-detail-btn:hover:after{
  color: #fafafa;
}
#root input[type=number]{
  position: relative;
  margin: 0 5px 0 0;
  max-width: calc(100% - 70px);
}
